import React, { useEffect, useState } from "react";
import styles from "../../styles/findcourse/SelectCommon.module.css";

const AREA_OBJECT = {
  11: "",
  26: "",
  27: "",
  28: "",
  29: "",
  30: "",
  31: "",
  36: "",
  41: "",
  42: "",
  43: "",
  44: "",
  45: "",
  46: "",
  47: "",
  48: "",
  50: "",
};

const testArr1 = [
  { id: 1, label: "test1-1" },
  { id: 2, label: "test1-2" },
  { id: 3, label: "test1-3" },
  { id: 4, label: "test1-4" },
  { id: 5, label: "test1-5" },
];

const AREA_ARRAY = Object.entries(AREA_OBJECT).map(([k, v]) => ({
  id: k,
  label: v,
}));

const AreaSelect = ({ setArea }) => {
  const [selState, setSelState] = useState({});
  const [selectedArea, setSelectedArea] = useState();
  const [subList, setSubList] = useState([]);

  useEffect(() => {
    //    State    
    if (!selState) {
      return;
    }
    // fetch 
    setSubList(testArr1);
  }, [selState]);

  useEffect(() => {
    if (!selectedArea) {
      return;
    }
    setArea(selectedArea);
  }, [selectedArea]);

  return (
    <div className={styles.selectListWrapper}>
      <ul className={styles.upperList}>
        {AREA_ARRAY.map(({ id, label }) => (
          <li key={id}>
            <button
              onClick={() => {
                setSelState({ id: id, label: label });
              }}
            >
              {label}
            </button>
          </li>
        ))}
      </ul>
      <ul className={styles.lowerList}>
        {subList.map(({ id, label }) => (
          <li key={id}>
            <button
              onClick={() => {
                setSelectedArea(selState.label + " " + label);
              }}
            >
              {label}
            </button>
          </li>
        ))}
        <>{selectedArea}</>
      </ul>
    </div>
  );
};

export default AreaSelect;
