import React, { useRef } from "react";
import styles from "../../styles/study/StudyReg.module.css";
import Box from "@mui/material/Box";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import FormControl from "@mui/material/FormControl";
import Select from "@mui/material/Select";
import TextField from "@mui/material/TextField";
import { Link } from "react-router-dom";

const StudyReg = () => {
  const ITEM_HEIGHT = 48;
  const ITEM_PADDING_TOP = 8;
  const MenuProps = {
    PaperProps: {
      style: {
        maxHeight: ITEM_HEIGHT * 3.8 + ITEM_PADDING_TOP,
        width: 120,
      },
    },
  };

  const [maxMemberCnt, setMaxMemberCnt] = React.useState("");

  const handleChange = (event) => {
    setMaxMemberCnt(event.target.value);
  };

  const menuItemList = () => {
    const result = [];
    for (let index = 1; index < 11; index++) {
      result.push(
        <MenuItem key={index} value={index}>
          {index}
        </MenuItem>
      );
    }
    return result;
  };

  const defaultContentValue = `       . 
         . 
  
  [   ]
  
    :
    : 
    () :
     :
    :
    : 
    : 
       . (,  ,  .)`;

  const readImage = (file) => {
    //     
    if (file) {
      // FileReader  
      const reader = new FileReader();

      //    
      reader.onload = (e) => {
        const preImg = document.getElementById("studyImgPreview");
        preImg.src = e.target.result;
      };

      reader.readAsDataURL(file);
    }
  };

  return (
    <div className={styles.studyRegContainer}>
      <div className={styles.leftContainer}>
        <img
          className={styles.studyImgPreview}
          src="https://velog.velcdn.com/images/kshired/post/d8a48a1f-4106-480f-8307-d20eae1f9486/image.png"
          alt=""
          id="studyImgPreview"
          title="  ."
          onClick={() => {
            document.getElementById("uploadImg").click();
          }}
          style={{ cursor: "pointer" }}
        ></img>
        <div className={styles.selectorWrapper}>
          <Box
            sx={{
              minWidth: 120,
              "& .MuiOutlinedInput-root": {
                "&.Mui-focused fieldset": {
                  borderColor: "#8cbf75",
                },
              },
            }}
          >
            <FormControl fullWidth>
              <InputLabel
                id="maxSelectLabel"
                sx={{
                  "&.MuiOutlinedInput-root": {
                    "&.Mui-focused fieldset": {
                      borderColor: "#8cbf75",
                    },
                  },
                  "&.MuiInputLabel-root": {
                    "&.Mui-focused": {
                      color: "#1d5902",
                    },
                  },
                }}
              >
                
              </InputLabel>
              <Select
                labelId="maxSelectLabel"
                id="maxSelect"
                value={maxMemberCnt}
                label="MaxMemberCnt"
                onChange={handleChange}
                MenuProps={MenuProps}
              >
                {menuItemList()}
              </Select>
            </FormControl>
          </Box>
        </div>
        <input
          hidden
          type="file"
          className={styles.uploadImg}
          id="uploadImg"
          onChange={(e) => {
            readImage(e.target.files[0]);
          }}
        ></input>
      </div>
      <div className={styles.rightContainer}>
        <Box
          component="form"
          sx={{
            "& > :not(style)": { m: 1, width: "750px" },
          }}
          noValidate
          autoComplete="off"
        >
          <TextField
            id="studyTitleInput"
            label=""
            variant="outlined"
            sx={{
              "& .MuiOutlinedInput-root": {
                "&.Mui-focused fieldset": {
                  borderColor: "#8cbf75",
                },
              },
              "& .MuiInputLabel-root": {
                "&.Mui-focused": {
                  color: "#1d5902",
                },
              },
            }}
          />
        </Box>
        <Box
          component="form"
          sx={{
            "& .MuiTextField-root": { m: 1, width: "750px" },
          }}
          noValidate
          autoComplete="off"
        >
          <TextField
            id="studyContentInput"
            label=""
            multiline
            rows={15}
            defaultValue={defaultContentValue}
            sx={{
              "& .MuiOutlinedInput-root": {
                "&.Mui-focused fieldset": {
                  borderColor: "#8cbf75",
                },
              },
              "& .MuiInputLabel-root": {
                "&.Mui-focused": {
                  color: "#1d5902",
                },
              },
            }}
          />
        </Box>
        <Box
          component="form"
          sx={{
            "& > :not(style)": { m: 1, width: "750px" },
          }}
          noValidate
          autoComplete="off"
        >
          <TextField
            id="studyContactInput"
            label=""
            variant="outlined"
            sx={{
              "& .MuiOutlinedInput-root": {
                "&.Mui-focused fieldset": {
                  borderColor: "#8cbf75",
                },
              },
              "& .MuiInputLabel-root": {
                "&.Mui-focused": {
                  color: "#1d5902",
                },
              },
            }}
          />
        </Box>
        <div className={styles.submitBtnWrapper}>
          <Link to={".."}>
            <button className={styles.studyRegCancel}></button>
          </Link>

          <button type="submit" className={styles.studyRegBtn}>
            
          </button>
        </div>
      </div>
    </div>
  );
};

export default StudyReg;
