import { Close, PhotoCamera } from "@mui/icons-material";
import {
  Avatar,
  Button,
  createTheme,
  IconButton,
  InputAdornment,
  OutlinedInput,
} from "@mui/material";
import { useState } from "react";

import styles from "../../styles/mypage/ProfileMdf.module.css";

function ProfileMdf() {
  const [user, setUser] = useState({
    userId: "thisisId",
    userPw: "dd",
    userName: "",
    userTel: "010-0000-0000",
    userEmail: "801@bitcamp.com",
    nickname: "",
    profilePath: "",
  });

  // mui button  
  const theme = createTheme({
    palette: {
      primary: {
        main: "#1d5902",
        contrastText: "#fff",
      },
      secondary: {
        main: "#555",
      },
      lightgreen: {
        main: "#8cbf75",
      },
    },
  });

  const [imageSrc, setImageSrc] = useState("");

  //    
  const encodeFileToBase64 = (e, file) => {
    e.target.value = "";
    const reader = new FileReader();
    reader.readAsDataURL(file);
    return new Promise((resolve) => {
      reader.onload = () => {
        setImageSrc(reader.result);
        resolve();
      };
    });
  };

  //     avatar src 
  function profilePicDeleteHandler() {
    setImageSrc(null);
  }

  const [nicknameValue, setNicknameValue] = useState(user.nickname);

  //  x   
  function resetNicknameHandler() {
    setNicknameValue("");
  }
  //   
  const onChangeNickname = (e) => {
    setNicknameValue(e.target.value);
  };

  // form submit      
  const [nicknameError, setNicknameError] = useState(false);

  const userProfileEdit = (e) => {
    e.preventDefault();
    const data = new FormData(e.target);
    const userNickname = data.get("nickname");
    console.log(userNickname);

    if (userNickname === null || userNickname === "") {
      setNicknameError(true);
    } else {
      alert(".");
      window.location.replace("/mypage");
    }
  };

  return (
    <div>
      <form onSubmit={userProfileEdit}>
        <div className={styles.mdfContainer}>
          <h1 className={styles.mdfTitle}> </h1>
          <Avatar
            alt="profile"
            src={imageSrc || "/upload/profile/farmer.png"}
            sx={{ width: 160, height: 160 }}
            className={styles.avatar}
          />

          <div className={styles.profilePicMdfBtns}>
            <Button
              variant="outlined"
              color="secondary"
              component="label"
              theme={theme}
              startIcon={<PhotoCamera color="action" />}
            >
               
              <input
                hidden
                id="miribogi"
                accept="image/*"
                type="file"
                onChange={(e) => {
                  encodeFileToBase64(e, e.target.files[0]);
                }}
              />
            </Button>
            <Button
              variant="outlined"
              color="secondary"
              theme={theme}
              className={styles.profilePicDeleteBtn}
              onClick={profilePicDeleteHandler}
            >
              
            </Button>
          </div>

          <div className={styles.nickname}>
            <h2 className={styles.nicknameTitle}></h2>
          </div>
          <OutlinedInput
            type="text"
            name="nickname"
            id="nickname"
            theme={theme}
            color="lightgreen"
            value={nicknameValue}
            onChange={onChangeNickname}
            placeholder=" ."
            className={styles.nicknameInput}
            error={nicknameError}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  aria-label="delete"
                  className={styles.nicknameCleanBtn}
                  onClick={resetNicknameHandler}
                >
                  <Close fontSize="small" />
                </IconButton>
              </InputAdornment>
            }
          />

          <div className={styles.profileMdfBtns}>
            <Button
              variant="outlined"
              color="primary"
              href="/mypage"
              theme={theme}
              className={styles.profileCancelBtn}
            >
              
            </Button>
            <Button
              type="submit"
              color="primary"
              variant="contained"
              theme={theme}
              className={styles.profileApplyBtn}
            >
              
            </Button>
          </div>
        </div>
      </form>
    </div>
  );
}

export default ProfileMdf;
