import React, { useState } from "react";
import styles from "../../styles/share/commentContainer.module.css";

//1.   State 
const CommentContainer = ({ commentArray, addCommentArray }) => {
  const [comment, setComment] = useState(""); //comment(state) setState state  
  const onChange = (event) => setComment(event.target.value); //   state 

  const onSubmit = (event) => {
    event.preventDefault(); //   x
    if (comment === "") {
      return;
    }
    //     
    addCommentArray(commentArray.concat(comment));
    setComment(""); //setComment  setState   submit    
  };

  return (
    <>
      <div className={styles.inputContainer} onSubmit={onSubmit}>
        <form className={styles.inputForm}>
          <input
            type="text"
            placeholder=" "
            value={comment}
            onChange={onChange}
          />
          <button className={styles.commentRegBtn}></button>
        </form>
      </div>
    </>
  );
};

export default CommentContainer;
